
import router from '../router'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'


export default {
  components: {
    Group,
    Cell,
    Selector,
    XTextarea,
    Datetime,
    Checker,
    CheckerItem,
    Divider,
    XButton,
    Confirm
  },
  data () {
    return {
      defaultValue: '教务处',
      list: ['教务处', '后勤'],
      beginTime: '',
      endTime: '',
      demo1: '',
      textarea: '',
      leaveType: ['调休', '其他', '迟到', '早退', '等等']
    }
  },
  methods: {
    onChange (val) {
      console.log(val)
    },
    onEvent (event) {
      console.log('on', event)
    },
    change (value) {
      console.log('change', value)
    },
    history () {
      console.log('查看历史')
    },
    sure () {
      var that = this
      console.log(this.beginTime, this.endTime)
      if (this.textarea === '') {
        vuxalert(that, '提示', '请输入请假内容')
      } else if (this.beginTime === '') {
        vuxalert(that, '提示', '请输入开始时间')
      } else if (this.endTime === '') {
        vuxalert(that, '提示', '请输入结束时间')
      } else if (new Date(this.beginTime) >= new Date(this.endTime)) {
        vuxalert(that, '提示', '结束时间应大于开始时间')
      } else if (this.demo1 === '') {
        vuxalert(that, '提示', '请选择请假类型')
      } else {
        vuxconfirm(that, '提示', '确定要提交请假吗？')
      }
    }
  }
}
function vuxalert (dom, title, content) {
  dom.$vux.alert.show({
    title: title,
    content: content,
    onShow () {
      console.log('showing')
    },
    onHide () {
      console.log('hiding')
    }
  })
}
function vuxconfirm (dom, title, content) {
  dom.$vux.confirm.show({
    title: title,
    content: content,
    hideOnBlur: true,
    onShow () {
      console.log('show')
    },
    onHide () {
      console.log('hide')
    },
    onCancel () {
      console.log('cancel')
    },
    onConfirm () {
      console.log('confrim')
      router.push('sure')
    }
  })
}



